import React, {Component} from 'react';
import './App.css';
import FuelManagmentApp from './components/FuelManagmentApp';
import {BrowserRouter, Route, Switch} from "react-router-dom";
import HomeComponent from "./components/HomeComponent";
import AuthenticatedRoute from "./components/AuthenticatedRoute";
import AddRefuelComponent from "./components/AddRefuelComponent";
import RefuelsListComponent from "./components/RefuelsListComponent";
import StatsSummaryComponent from "./components/StatsSummaryComponent";
import AddCarComponent from "./components/AddCarComponent";
import CarListComponent from "./components/CarListComponent";
import RegisterComponent from "./components/RegisterComponent";
import LoginComponent from "./components/LoginComponent";
import LogoutComponent from "./components/LogoutComponent";

class App extends Component {
  render() {
    return (
      <div className="container">
        <BrowserRouter basemname={`${process.env.PUBLIC_URL}`}>
          <Switch>
            <Route exact path="" component={HomeComponent}/>
            <AuthenticatedRoute path="/cars/:carId/refuels/:refuelId" component={AddRefuelComponent}/>
            <AuthenticatedRoute path="/cars/:carId/refuels/" component={RefuelsListComponent}/>
            <AuthenticatedRoute path="/cars/:carId/stats" component={StatsSummaryComponent}/>
            <AuthenticatedRoute path="/cars/:carId" component={AddCarComponent}/>
            <AuthenticatedRoute path="/cars" component={CarListComponent}/>
            <Route path="/register" component={RegisterComponent}/>
            <Route path="/login" component={LoginComponent}/>
            <AuthenticatedRoute path="/logout" component={LogoutComponent}/>
          </Switch>
        </BrowserRouter>
      </div>
    )
  }
}

export default App;
