import { Component } from "react";
import AuthenticationService from "./AuthenticationService";
import {Link} from "react-router-dom";
import LoginComponent from "./LoginComponent";
import RegisterComponent from "./RegisterComponent";
import LogoutComponent from "./LogoutComponent";

class AppNavbar extends Component {
    render() {
        let isUserLoggedIn = AuthenticationService.isUserLoggedIn();
        let loginOrLogoutButton;
        let registerButton = "";
        if (isUserLoggedIn) {
            loginOrLogoutButton = <Link className="nav-item nav-link" to="/logout">logout</Link>
        } else {
            loginOrLogoutButton = <Link className="nav-item nav-link" to="/login">login</Link>
            registerButton = <Link className="nav-item nav-link" to="/register">register</Link>
        }
        return (
            <nav className="navbar navbar-expand-lg navbar-dark bg-dark">
                <span className="navbar-brand">Navbar</span>
                <button className="navbar-toggler" type="button" data-toggle="collapse" data-target="#navbarNavAltMarkup" aria-controls="navbarNavAltMarkup" aria-expanded="false" aria-label="Toggle navigation">
                    <span className="navbar-toggler-icon"></span>
                </button>
                <div className="collapse navbar-collapse" id="navbarNavAltMarkup">
                    <div className="navbar-nav">
                        <a className="nav-item nav-link" href="/">Home</a>
                        <a className="nav-item nav-link" href="/cars">cars</a>
                        {loginOrLogoutButton}
                        {registerButton}
                    </div>
                </div>
            </nav>
        )
    }
}

export default AppNavbar